* {
    box-sizing: border-box;
    margin: 0 0;
}

html {
    width: 100%;
    min-height: 100%;
}

body {
    width: 100%;
    min-height: 100vh;
    height: 100%;
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, San Francisco, Helvetica Neue, Helvetica, Ubuntu, Roboto, Noto,
        Segoe UI, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.withSpinner {
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 3px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    border-top-color: #fff;
    animation: spin 1s ease-in-out infinite;
    -webkit-animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
    to {
        -webkit-transform: rotate(360deg);
    }
}
#root {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
}

@media (max-width: 480px) {
    .root--fallback-background {
        background: url(../assets/images/background_mobile.jpg) no-repeat center center fixed;
        background-size: cover;
    }
}
@media (min-width: 481px) and (max-width: 759px) {
    .root--fallback-background {
        background: url(../assets/images/background_small.jpg) no-repeat center center fixed;
        background-size: cover;
    }
}
@media (min-width: 1601px) {
    .root--fallback-background {
        background: url(../assets/images/background.jpg) no-repeat center center fixed;
        background-size: cover;
    }
}
@media (min-width: 760px) and (max-width: 1024px) {
    .root--fallback-background {
        background: url(../assets/images/background_tablet.jpg) no-repeat center center fixed;
        background-size: cover;
    }
}
@media (min-width: 1025px) and (max-width: 1600px) {
    .root--fallback-background {
        background: url(../assets/images/background_desktop.jpg) no-repeat center center fixed;
        background-size: cover;
    }
}

.fade-in {
    animation: fadeIn ease-in-out 0.3s;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.sr-only {
    position: absolute;
    left: -10000px;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
}

*:focus {
    outline: 2px ridge #0ca4f6;
    outline-offset: 1px;
    z-index: 2000;
}

.visible {
    visibility: visible !important;
}

.hidden {
    visibility: hidden !important;
}

.active {
    display: block !important;
}

