    z-index: 1000;
}

.modal-close__button {
    position: relative;
    transition: all 0.1s ease-in-out;
    opacity: 0.8;
    fill: #fff;
    box-shadow: none;
    border: none;
    padding: 0;
    background-color: initial;
    text-align: inherit;
    cursor: pointer;
}
.modal-close__button:hover {
    opacity: 1;
    box-shadow: none;
}

.modal-close__svg {
    display: block;
    width: 24px;
    height: 24px;
}

.slideitem {
    display: inline;
}

.noHints,
.noHints--short {
    height: 54px;
    border-radius: 54px;
    background-color: #f5f5f5;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 6px;
}

.noHints--short {
    width: 60%;
    margin: 12px auto;
    background-color: #eeeeee;
}

.images-hint {
    min-width: 120px;
    display: inline-block;
    margin: 0 8px 8px 0;
    cursor: pointer;
}
.images-hint:first-letter {
    text-transform: uppercase;
}
